import React, { useMemo, useRef, useState } from "react";

import type { AIMessage } from "../../lib/ai/types";
import { useAI } from "../../lib/ai/useAI";

import { Button } from "../ui/Button";
import { Card } from "../ui/Card";

import clsx from "clsx";

export const ChatBox: React.FC = () => {
  const { chat, loading, error, provider } = useAI();
  const [messages, setMessages] = useState<AIMessage[]>([]);

  const [input, setInput] = useState("");

  const endRef = useRef<HTMLDivElement>(null);

  const system = useMemo<AIMessage>(
    () => ({ role: "system", content: "You are a helpful assistant." }),
    []
  );

  async function send() {
    const text = input.trim();
    if (!text) return;

    const next = [...messages, { role: "user", content: text } as AIMessage];
    setInput("");

    const reply = await chat([system, ...next]);
    setMessages([...next, { role: "assistant", content: reply }]);

    endRef.current?.scrollIntoView({ behavior: "smooth" });
  }

  return (
    <Card className="space-y-4">
      <div className="flex items-center gap-2">
        <h3 className="mr-1 text-lg font-semibold text-slate-900 dark:text-slate-100">
          üí¨ ChatBox
        </h3>
      </div>

      <div
        className={clsx(
          "w-full min-h-40 max-h-112 overflow-auto rounded-xl border p-3 text-sm",
          "border-slate-200 bg-slate-50",
          "dark:border-slate-700 dark:bg-slate-900 dark:text-slate-100"
        )}
      >
        {messages.length === 0 && (
          <div className="text-slate-500 dark:text-slate-400">
            Start the conversation
          </div>
        )}

        {messages.map((m, i) => (
          <div
            key={i}
            className={clsx(
              "mb-2 flex",
              m.role === "user" ? "justify-end" : "justify-start"
            )}
          >
            <div
              className={clsx(
                "max-w-[80%] rounded-2xl px-3 py-2 shadow-sm whitespace-pre-wrap wrap-break-wordbreak-words",
                m.role === "user"
                  ? "bg-indigo-600 text-white dark:bg-indigo-500"
                  : "bg-white text-slate-900 border border-slate-200 dark:bg-slate-800 dark:text-slate-100 dark:border-slate-700"
              )}
            >
              {m.content}
            </div>
          </div>
        ))}

        <div ref={endRef} />
      </div>

      <div className="flex w-full gap-2">
        <input
          aria-label="Type a message"
          className={clsx(
            "flex-1 rounded-xl border px-3 py-2 text-sm outline-none",
            "placeholder:text-slate-400",
            "border-slate-300 bg-white focus:ring-2 focus:ring-indigo-500 text-slate-900",
            "dark:border-slate-700 dark:bg-slate-900 dark:text-slate-100 dark:placeholder:text-slate-500 dark:focus:ring-indigo-400"
          )}
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === "Enter" && !e.shiftKey) {
              e.preventDefault();
              send();
            }
          }}
          placeholder="Type a message..."
        />

        <Button disabled={loading || input.trim().length === 0} onClick={send}>
          {loading ? "‚Ä¶" : "Send"}
        </Button>
      </div>

      <div className="flex items-center gap-3">
        {provider && (
          <span className="rounded-full border border-slate-200 bg-slate-100 px-3 py-1 text-xs text-slate-700 dark:border-slate-700 dark:bg-slate-800 dark:text-slate-300">
            via {provider}
          </span>
        )}
        {error && <p className="text-xs text-slate-400">{error}</p>}
      </div>
    </Card>
  );
};
